.oauth2-authorization-proxy-login * {
  margin: 10px 0; }

.oauth2-authorization-proxy-header .user-avatar {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  object-fit: cover; }

@media screen and (min-width: 960px) {
  .oauth2-authorization-proxy-header .user-avatar {
    height: 50px;
    width: 50px; } }

.oauth2-authorization-proxy-header .username {
  display: inline-block;
  margin-left: 15px;
  margin-right: 15px; }

.oauth2-provider-logo {
  max-height: 40px;
  margin: 0 15px;
  cursor: pointer; }
